import { Component, OnInit } from '@angular/core';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CustomerPerformanceHttpService } from 'app/pages/tzb/custom/http/customer-performance/customer-performance.http.service';
import { Message } from 'primeng/primeng';
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { MessageService } from "primeng/components/common/messageservice";
import { CustomerPerformanceService } from "app/pages/tzb/custom/customer-performance/service/customer-performance.service";
import { ActivatedRoute } from '@angular/router';

@Component({
    selector: 'app-badness-propertyDistribution',
    templateUrl: './badness-propertyDistribution.component.html',
    styleUrls: ['./badness-propertyDistribution.component.scss'],
    providers: [Commfunc]
})

export class BadnessPropertyDistributionComponent implements OnInit {

    commonHeader: any;
    loadingShow: any = { flag: false }; // loading
    msgs: Message[] = [];

    startDate: any = this.commfunc.transDateN(new Date().setTime(new Date().getTime() - 1000 * 60 * 60 * 24 * 30));
    endDate: any = this.commfunc.transDateN(new Date());

    option1: any;   // -
    option2: any;   // -
    option3: any;   // -
    option4: any;   // -
    option5: any;   // -
    option6: any;   // -
    option7: any;   // -
    option8: any;   // -

    pTooltip1: string = '';
    pTooltip2: string = '';
    pTooltip3: string = '';
    pTooltip4: string = '';
    pTooltip5: string = '';
    pTooltip6: string = '';
    pTooltip7: string = '';
    pTooltip8: string = '';

    industryList: any[] = [//
        { value: 'A', label: '' },
        { value: 'B', label: '' },
        { value: 'C', label: '' },
        { value: 'D', label: '' },
        { value: 'E', label: '' },
        { value: 'F', label: '' },
        { value: 'G', label: '' },
        { value: 'H', label: '' },
        { value: 'I', label: '' },
        { value: 'J', label: '' },
        { value: 'K', label: '' },
        { value: 'L', label: '' },
        { value: 'M', label: '' },
        { value: 'N', label: '' },
        { value: 'O', label: '' },
        { value: 'P', label: '' },
        { value: 'Q', label: '' },
        { value: 'R', label: '' },
        { value: 'S', label: '' },
        { value: 'T', label: '' },
        { value: 'Z', label: '' }
    ];
    ageList: any[] = [//
        { value: '1', label: '25' },
        { value: '2', label: '25-35' },
        { value: '3', label: '35-45' },
        { value: '4', label: '45-55' },
        { value: '5', label: '55-65' },
        { value: '6', label: '65' }
    ];
    productList: any[] = [//
        { value: '01', label: ' ' },
        { value: '02', label: ' ' },
        { value: '03', label: ' ' },
        { value: '04', label: ' ' },
        { value: '05', label: ' ' },
        { value: '06', label: ' ' },
        { value: '09', label: '' }
    ];
    guaranteeList: any[] = [//
        { value: '1201', label: '' },
        { value: '1301', label: '' },
        { value: '1302', label: '' },
        { value: '1101', label: '' },
        { value: '1139', label: '' },
        { value: '-1', label: '' },
        { value: '1001', label: '' }
    ]

    constructor(
        public httpService: CustomerPerformanceHttpService,
        public commfunc: Commfunc,
        private commonFunc: Customtools,
        private messageService: MessageService,
        public customerPerformanceService: CustomerPerformanceService,
        public activatedRoute: ActivatedRoute,
    ) {
        this.commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        //
        this.option1 = this.customerPerformanceService.returnOption2({ title: '-', name: '', data: [0] });
        this.option2 = this.customerPerformanceService.returnOption2({ title: '-', name: '', data: [0] });
        this.option3 = this.customerPerformanceService.returnOption2({ title: '-', name: '', data: [0] });
        this.option4 = this.customerPerformanceService.returnOption2({ title: '-', name: '', data: [0] });
        this.option5 = this.customerPerformanceService.returnOption2({ title: '-', name: '', data: [0] });
        this.option6 = this.customerPerformanceService.returnOption2({ title: '-', name: '', data: [0] });
        this.option7 = this.customerPerformanceService.returnOption3({ title: '', xAxisName: '', yAxisName: '', series: [] });
        this.option8 = this.customerPerformanceService.returnOption3({ title: '', xAxisName: '', yAxisName: '', series: [] });
    }

    ngOnInit() {
        let urlParams;
        this.activatedRoute.params.subscribe(params => {
            urlParams = params;
        });
        // console.log(urlParams);
        if (urlParams && JSON.stringify(urlParams) != '{}') {
            if (urlParams.flag != '1') {
                this.queryAll('CP');
                this.queryAll('HY');
                this.queryAll('DB');
                this.queryAll('NL');
            }
        }
    }

    outValue(event) {
        let objTemp = this.customerPerformanceService.returnTime(event.queryCondition, event.index);
        if (event.index == 1) {
            objTemp = this.customerPerformanceService.returnTime(event.queryCondition, event.index);
        } else if (event.index == 2) {
            objTemp = event.queryCondition;
        }
        this.startDate = objTemp.startDate;
        this.endDate = objTemp.endDate;
        console.log('startDate:', this.startDate);
        console.log('endDate:', this.endDate);

        this.queryAll('CP');
        this.queryAll('HY');
        this.queryAll('DB');
        this.queryAll('NL');
    }


    queryAll(badnessType) {
        const params = {
            tellerId: this.commonHeader.userId,
            startDate: this.startDate,
            endDate: this.endDate,
            badnessType: badnessType
        }
        const configParam = {
            httpService: this.httpService,
            loadingFlag: true,
            messageFlag: false,
            inputParam: params,
            message: this.messageService,
            injectName: 'badnessDistributeQuery',
            loadingShow: this.loadingShow,
            messageInfo: '',
        }
        this.commonFunc.haddleData(configParam, (bodyData) => {
            let data1 = [];
            let data2 = [];
            let data3 = [];
            let data4 = [];
            let data5 = [];
            let data6 = [];
            if (bodyData.disNumList && bodyData.disNumList.length > 0) {
                bodyData.disNumList.forEach(el => {
                    let dataObj = {};
                    dataObj['name'] = el.key;
                    dataObj['value'] = el.disNum;
                    data1.push(dataObj);
                    data3.push(el.key);
                    data4.push(el.disNum);
                });
            }

            if (bodyData.disAmtList && bodyData.disAmtList.length > 0) {
                bodyData.disAmtList.forEach(el => {
                    let dataObj = {};
                    dataObj['name'] = el.key;
                    dataObj['value'] = el.disAmt;
                    data2.push(dataObj);
                    data5.push(el.key);
                    data6.push(el.disAmt);
                });
            }
            switch (badnessType) {
                case 'CP':
                    if (data1 && data1.length != 0) {
                        data1.forEach(element => {
                            this.productList.forEach(element2 => {
                                if (element.name == element2.value) {
                                    element.name = element2.label;
                                }
                            });
                        });
                    }
                    if (data2 && data2.length != 0) {
                        data2.forEach(element => {
                            this.productList.forEach(element2 => {
                                if (element.name == element2.value) {
                                    element.name = element2.label;
                                }
                            });
                        });
                    }
                    console.log(data1);
                    this.option1 = {
                        title: {
                            text: '-',
                            left: 'center',
                            y: '10px',
                            textStyle: {
                                fontWeight: '500',
                                fontSize: '16'
                            }
                        },
                        series: [
                            {
                                data: data1,
                                type: 'pie',
                                radius: ['50%', '70%'],
                                label: {
                                    normal: {
                                        formatter: '{b} {c}'
                                    }
                                }
                            }
                        ]
                    }

                    this.option2 = {
                        title: {
                            text: '-',
                            left: 'center',
                            y: '10px',
                            textStyle: {
                                fontWeight: '500',
                                fontSize: '16'
                            }
                        },
                        series: [
                            {
                                data: data2,
                                type: 'pie',
                                radius: ['50%', '70%'],
                                label: {
                                    normal: {
                                        formatter: '{b} {c}'
                                    }
                                }
                            }
                        ]
                    }
                    break;

                case 'HY':
                    if (data1 && data1.length != 0) {
                        data1.forEach(element => {
                            this.industryList.forEach(element2 => {
                                if (element.name == element2.value) {
                                    element.name = element2.label;
                                }
                            });
                        });
                    }
                    if (data2 && data2.length != 0) {
                        data2.forEach(element => {
                            this.industryList.forEach(element2 => {
                                if (element.name == element2.value) {
                                    element.name = element2.label;
                                }
                            });
                        });
                    }
                    this.option3 = {
                        title: {
                            text: '-',
                            left: 'center',
                            y: '10px',
                            textStyle: {
                                fontWeight: '500',
                                fontSize: '16'
                            }
                        },
                        series: [
                            {
                                data: data1,
                                type: 'pie',
                                radius: ['50%', '70%'],
                                label: {
                                    normal: {
                                        formatter: '{b} {c}'
                                    }
                                }
                            }
                        ]
                    }

                    this.option4 = {
                        title: {
                            text: '-',
                            left: 'center',
                            y: '10px',
                            textStyle: {
                                fontWeight: '500',
                                fontSize: '16'
                            }
                        },
                        series: [
                            {
                                data: data2,
                                type: 'pie',
                                radius: ['50%', '70%'],
                                label: {
                                    normal: {
                                        formatter: '{b} {c}'
                                    }
                                }
                            }
                        ]
                    }
                    break;
                case 'DB':
                    if (data1 && data1.length != 0) {
                        data1.forEach(element => {
                            this.guaranteeList.forEach(element2 => {
                                if (element.name == element2.value) {
                                    element.name = element2.label;
                                }
                            });
                        });
                    }
                    if (data2 && data2.length != 0) {
                        data2.forEach(element => {
                            this.guaranteeList.forEach(element2 => {
                                if (element.name == element2.value) {
                                    element.name = element2.label;
                                }
                            });
                        });
                    }
                    this.option5 = {
                        title: {
                            text: '-',
                            left: 'center',
                            y: '10px',
                            textStyle: {
                                fontWeight: '500',
                                fontSize: '16'
                            }
                        },
                        series: [
                            {
                                data: data1,
                                type: 'pie',
                                radius: ['50%', '70%'],
                                label: {
                                    normal: {
                                        formatter: '{b} {c}'
                                    }
                                }
                            }
                        ]
                    }

                    this.option6 = {
                        title: {
                            text: '-',
                            left: 'center',
                            y: '10px',
                            textStyle: {
                                fontWeight: '500',
                                fontSize: '16'
                            }
                        },
                        series: [
                            {
                                data: data2,
                                type: 'pie',
                                radius: ['50%', '70%'],
                                label: {
                                    normal: {
                                        formatter: '{b} {c}'
                                    }
                                }
                            }
                        ]
                    }
                    break;

                case 'NL':
                    if (data3 && data3.length != 0) {
                        data3.forEach((element, i) => {
                            this.ageList.forEach(element2 => {
                                if (element == element2.value) {
                                    data3[i] = element2.label;
                                }
                            });
                        });
                    }
                    if (data5 && data5.length != 0) {
                        data5.forEach((element, i) => {
                            this.ageList.forEach(element2 => {
                                if (element == element2.value) {
                                    data5[i] = element2.label;
                                }
                            });
                        });
                    }
                    this.option7 = {
                        title: {
                            text: '',
                            left: 'center',
                            y: '10px',
                            textStyle: {
                                fontWeight: 'normal'
                            }
                        },
                        xAxis: {
                            name: '',
                            type: 'category',
                            data: data3,
                            // axisLine: {
                            //     show: true,
                            //     symbol: ['none', 'arrow'],
                            //     symbolSize: [8, 8],
                            //     symbolOfffset: [0, 7]
                            // },
                            axisTick: {
                                show: false
                            }
                        },
                        yAxis: {
                            name: '',
                            type: 'value',
                            nameGap: 20,
                            scale: true,
                            splitArea: {
                                show: true
                            },
                            axisTick: {
                                show: false
                            },
                            axisLine: {
                                show: false
                            },
                        },
                        series: [
                            {
                                data: data4,
                                type: 'bar',
                                legendHoverLink: true,
                                barWidth: '40',
                                barCategoryGap: '20%',
                                itemStyle: {
                                    normal: {
                                        label: {
                                            show: true,
                                            position: 'top'
                                        }
                                    }
                                }
                            }
                        ]
                    };

                    this.option8 = {
                        title: {
                            text: '',
                            left: 'center',
                            y: '10px',
                            textStyle: {
                                fontWeight: 'normal'
                            }
                        },
                        xAxis: {
                            name: '',
                            type: 'category',
                            data: data5,
                            // axisLine: {
                            //     show: true,
                            //     symbol: ['none', 'arrow'],
                            //     symbolSize: [8, 8],
                            //     symbolOfffset: [0, 7]
                            // },
                            axisTick: {
                                show: false
                            }
                        },
                        yAxis: {
                            name: '',
                            type: 'value',
                            nameGap: 20,
                            scale: true,
                            splitArea: {
                                show: true
                            },
                            axisTick: {
                                show: false
                            },
                            axisLine: {
                                show: false
                            },
                        },
                        series: [
                            {
                                data: data6,
                                type: 'bar',
                                legendHoverLink: true,
                                barWidth: '40',
                                barCategoryGap: '20%',
                                itemStyle: {
                                    normal: {
                                        label: {
                                            show: true,
                                            position: 'top'
                                        }
                                    }
                                }
                            }
                        ]
                    }
                    break
                default:
                    break;
            }

        })
    }

}