import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { CustomerPerformanceComponent } from './customer-performance.component';
import { IndicatorsProgressComponent } from './indicators-progress/indicators-progress.component';
import { IndicatorsAnalysisComponent } from './indicators-analysis/indicators-analysis.component';
import { PerformanceHomePagesComponent } from './performance-home-pages/performance-home-pages.component';
import { LoanTendcyChangeComponent } from './loan/loan-tendcyChange/loan-tendcyChange.component';
import { LoanProductCategoryComponent } from './loan/loan-productCategory/loan-productCategory.component';
import { LoanSmallClassProductComponent } from './loan/loan-smallClassProduct/loan-smallClassProduct.component';
import { LoanIndustryInvestmentComponent } from './loan/loan-industryInvestment/loan-industryInvestment.component';
import { LoanIncreasementalTendencyComponent } from './loan/loan-increasementalTendency/loan-increasementalTendency.component';
import { LoanGrowthGuideComponent } from './loan/loan-growthGuide/loan-growthGuide.component';
import { BadnessPropertyDistributionComponent } from "app/pages/tzb/custom/customer-performance/badness/badness-propertyDistribution/badness-propertyDistribution.component";
import { BadnessTendcyChangeComponent } from "app/pages/tzb/custom/customer-performance/badness/badness-tendcyChange/badness-tendcyChange.component";
import { FtpAfterDiscountComponent } from './ftp/ftp-afterDiscount/ftp-afterDiscount.component';
import { FtpBeforeDiscountComponent } from './ftp/ftp-beforeDiscount/ftp-beforeDiscount.component';
import { DepositTendcyChangeComponent } from "app/pages/tzb/custom/customer-performance/deposit/deposit-tendcyChange/deposit-tendcyChange.component";
import { DepositGrowthGuideComponent } from "app/pages/tzb/custom/customer-performance/deposit/deposit-growthGuide/deposit-growthGuide.component";
import { DepositIncreasementalTendencyComponent } from "app/pages/tzb/custom/customer-performance/deposit/deposit-increasementalTendency/deposit-increasementalTendency.component";
import { DepositIndustryInvestmentComponent } from "app/pages/tzb/custom/customer-performance/deposit/deposit-industryInvestment/deposit-industryInvestment.component";
import { DepositProductCategoryComponent } from "app/pages/tzb/custom/customer-performance/deposit/deposit-productCategory/deposit-productCategory.component";
import { DepositSmallClassProductComponent } from "app/pages/tzb/custom/customer-performance/deposit/deposit-smallClassProduct/deposit-smallClassProduct.component";
import { TaskQueryComponent } from 'app/pages/tzb/custom/achievement-task/task-query/task-query.component';

export const routes: Routes = [
    {
        path: '', component: CustomerPerformanceComponent, children: [
            { path: '', redirectTo: 'performance-home-pages', pathMatch: 'full' },
            { path: 'performance-home-pages', component: PerformanceHomePagesComponent },//
            { path: 'indicators-progress', component: IndicatorsProgressComponent },//
            { path: 'indicators-analysis', component: IndicatorsAnalysisComponent },//

            { path: 'deposit/tendcyChange', component: DepositTendcyChangeComponent },//-
            { path: 'deposit/productCategory', component: DepositProductCategoryComponent },//-
            { path: 'deposit/smallClassProduct', component: DepositSmallClassProductComponent },//-
            { path: 'deposit/savingNature', component: DepositIndustryInvestmentComponent },//-
            { path: 'deposit/increasementalTendency', component: DepositIncreasementalTendencyComponent },//-
            { path: 'deposit/growthGuide', component: DepositGrowthGuideComponent },//-

            { path: 'loan/tendcyChange', component: LoanTendcyChangeComponent },//
            { path: 'loan/productCategory', component: LoanProductCategoryComponent },
            { path: 'loan/smallClassProduct', component: LoanSmallClassProductComponent },
            { path: 'loan/industryInvestment', component: LoanIndustryInvestmentComponent },//--
            { path: 'loan/increasementalTendency', component: LoanIncreasementalTendencyComponent },
            { path: 'loan/growthGuide', component: LoanGrowthGuideComponent },

            { path: 'ftp/ftpBeforeDiscount', component: FtpBeforeDiscountComponent },//FTP--
            { path: 'ftp/ftpAfterDiscount', component: FtpAfterDiscountComponent },//FTP--

            { path: 'badness/tendcyChange', component: BadnessTendcyChangeComponent },//--
            { path: 'badness/propertyDistribution', component: BadnessPropertyDistributionComponent },//--

            { path: 'indicators-task-query', component: TaskQueryComponent },//
            { path: 'indicators-task-setting', loadChildren: '../achievement-task/task-setting/task-setting.module#TaskSettingModule' }//
        ]
    }
]

export const CustomerPerformanceRouting: ModuleWithProviders = RouterModule.forChild(routes);